Vue.use(VueAxios, axios)
export default {
  name: "SupportMessageComponent",
        data() {
            return {
                showForm: false,
                sentMessage: false,
                message: '',
                email: '',
                phone: '',
                error: false,
                formData : new FormData(),
                action : 'https://taleantai.com/taleantbot/',
                first : true,
